/* eslint-disable jsx-a11y/click-events-have-key-events */
import React, { useState, useEffect } from 'react';
import algosdk from 'algosdk';
import background from "./assets/background.png";

import Grid from './components/Grid';
import ColorPicker from './components/ColorPicker';
import Navbar from './components/Navbar';
import useStyles from './App.styles';

const appID = 117172492;

const colors = ['#ff4500', '#ffa800', '#ffd635', '#00a368', '#7eed56', '#2450a4', '#3690ea', 
                '#51e9f4', '#811e9f', '#b44ac0', '#ff99aa', '#9c6926', '#000000', '#898d90', '#d4d7d9', '#ffffff'];
const algodClient = new algosdk.Algodv2('', 'https://testnet-api.algonode.cloud', '');

const offCell = {
  color: '#ffffff',
  update: true
};

const updateCellsFromContract = async (cells) => {
  try {
    let res = await algodClient.getApplicationByID(appID).do();
    let raw_pxs = new Array(4761).fill(0);
    for (let i = 0; i < 49; i++) {
      if (res['params']['global-state'][i]['key'] !== 'YWlk') {
        let pos = 100*(atob(res['params']['global-state'][i]['key'])-1);
        let len = pos===48 ? 61 : 100;
        raw_pxs.splice(pos, len, ...Uint8Array.from(atob(res['params']['global-state'][i]['value']['bytes']), c => c.charCodeAt(0)));
      };
    };
    
    //console.log(atob(res['params']['global-state'][0]['key']));
    //console.log(raw_pxs);


    let pxs = [];
    for (let i = 0; i < raw_pxs.length; i++) {
      if (cells[i]["update"]) {
        pxs.push(
          {
            color: colors[raw_pxs[i]],
            update: true
          }
        );
      } else {
        pxs.push(cells[i])
      };
    };
    return pxs
  } catch {
    console.log("Failed to get application state.")
  };
};

const initialCells = Array.from({ length: 4761 }, () => offCell);

function App() {
  const [cells, setCells] = useState(initialCells);
  const [currentColor, setCurrentColor] = useState('#000000');
  const [changes, setChanges] = useState(0);
  const classes = useStyles();

  window.onload = () => updateCellsFromContract(cells).then(setCells); // fetch pixels immediately

  useEffect(() => {
    const interval = setInterval(() => {
      updateCellsFromContract(cells).then(setCells);
      // update the cost counter here as well because sometimes we register a click but overwrite it
      setChanges(cells?.reduce((sum, cell) => sum + !cell.update, 0));
    }, 1500);
    return () => clearInterval(interval)
  });
  
  return (
    <div className={classes.app} style={{ backgroundImage: `url(${background})` }}>
      <Navbar cells={cells} changes={changes} setChanges={setChanges} setCells={setCells} />
      <ColorPicker currentColor={currentColor} onSetColor={setCurrentColor} cells={cells} setCells={setCells} setChanges={setChanges} />
      <Grid cells={cells} setCells={setCells} currentColor={currentColor} />
      <div className={classes.logodiv}>
        <img className={classes.logo} height= "20vmin" src="/kinn_logo.svg" alt="kinn logo"/>
      </div>
    </div>
  );
}

export default App;
