import React from 'react';

import useStyles from './ColorPicker.styles';

const colors = ['#ff4500', '#ffa800', '#ffd635', '#00a368', '#7eed56', '#2450a4', '#3690ea', 
'#51e9f4', '#811e9f', '#b44ac0', '#ff99aa', '#9c6926', '#000000', '#898d90', '#d4d7d9', '#ffffff']

const clearAll = async ({ cells, setCells, setChanges }) => {
  setChanges(0);
  for (let i = 0; i < cells.length; i++) {
    if (!cells[i].update) {
      cells[i].color = "#ffffff";
      cells[i].update = true;
    };
  };
  setCells(cells)
}

const ColorPicker = ({ currentColor, onSetColor, cells, setCells, setChanges }) => {
  const classes = useStyles();
  const colorChange = (color) => {
    onSetColor(color);
  };

  return (
    <div className={classes.colorPicker}>
      {colors.map(function(c, i){
        return <button className={classes["c"+colors[i].slice(1)]} key={colors[i].slice(1)} onClick={() => colorChange(c)}></button>;
      })}
      <img className={classes.clear} src="clear.svg" alt="clear all pixels" onClick={() => clearAll({ cells, setCells, setChanges })}></img>
    </div>
  );
};

export default ColorPicker;
