const node = 'testnet'
const algodClient = new algosdk.Algodv2('', 'https://'+node+'-api.algonode.cloud', '');
algodClient.setIntEncoding("bigint")
const myAlgoWallet = new MyAlgoConnect();
const assetID = 109971296;
const appID = 117172492;
const appBuyID = 117364207;

const colors = ['#ff4500', '#ffa800', '#ffd635', '#00a368', '#7eed56', '#2450a4', '#3690ea', 
                '#51e9f4', '#811e9f', '#b44ac0', '#ff99aa', '#9c6926', '#000000', '#898d90', '#d4d7d9', '#ffffff'];

const connectWallet = async ({ setAddress, setHoldingString, setHoldingAmount, setOptedIn }) => {
  try {
    const accounts = await myAlgoWallet.connect();
    const addresses = accounts.map(account => account.address);
    setAddress(addresses[0]);
    let holding = await algodClient.accountAssetInformation(addresses[0], assetID).do();
    setHoldingString(String(holding["asset-holding"].amount/bigInt(1e6).value));
    setHoldingAmount(holding["asset-holding"].amount/bigInt(1e6).value);
    setOptedIn(true);
  } catch (err) {
    console.log("Not opted in.")
    setHoldingString("0");
    setHoldingAmount(0);
    setOptedIn(false);
    console.error(err);
  };
};

const concatUint8Array = (rawArr, uintArr) => {
  for (let i = 0; i < rawArr.length; i += 1) {
    uintArr.push(new Uint8Array(Buffer.from(algosdk.encodeUint64(rawArr[i]))))
  };

  // Get the total length of all arrays.
  let length = 0;
  uintArr.forEach(item => {
    length += item.length;
  });

  // Create a new array with total length and merge all source arrays.
  let mergedArray = new Uint8Array(length);
  let offset = 0;
  uintArr.forEach(item => {
    mergedArray.set(item, offset);
    offset += item.length;
  });
  return mergedArray
};

const buyPixels = async ({ address, setHoldingString, holdingAmount, setHoldingAmount, amount, optedIn, setModalIsOpenToFalse }) => {
  if (address) {
    // This will have to switch to a link to humbleswap once you mint $KINN and then transition to MainNet
    let receiver = "FGU3MBZAUFYVYO2JC33UBHGNDEOFNA5LSMXERVUO6MJEBGH6RN3OYACOTM";
    try {
    const appArgs = []
    appArgs.push(
      new Uint8Array(Buffer.from("buy")),
    )
    let params = await algodClient.getTransactionParams().do()
      params.fee = 1000;
      params.flatFee = true;

    // create unsigned asset transfer transaction
    let ptxn = algosdk.makePaymentTxnWithSuggestedParams(address, receiver, Math.round((amount/66)*1e6), undefined, undefined, params);

    // create unsigned noop transaction
    let nptxn = algosdk.makeApplicationNoOpTxn(address, params, appBuyID, appArgs, undefined, undefined, [assetID]);

    let txns;
    if (optedIn) {
      txns = [ptxn, nptxn]
    } else {
      let opttxn = algosdk.makeAssetTransferTxnWithSuggestedParams(address, address, undefined, undefined, 0, undefined, assetID, params);
      txns = [opttxn, ptxn, nptxn]
    };

    const groupID = algosdk.computeGroupID(txns)
    for (let i = 0; i < txns.length; i++) txns[i].group = groupID;

    const signedTxns = await myAlgoWallet.signTransaction(txns.map(txn => txn.toByte()));

    await algodClient.sendRawTransaction(signedTxns.map((i) => {return i.blob})).do();

    // update right away and then reupdate just to double check
    setHoldingString(String(bigInt(holdingAmount)+bigInt(Math.round(amount*1e6)/1e6)));
    setHoldingAmount(bigInt(holdingAmount)+bigInt(Math.round(amount*1e6)/1e6));
    setModalIsOpenToFalse()

    await new Promise(r => setTimeout(r, 8500));
    console.log("Bought!")

    } catch (err) {
      console.log(err)
    }

    let holding = await algodClient.accountAssetInformation(address, assetID).do();
    setHoldingString(String(holding["asset-holding"].amount/bigInt(1e6).value));
    setHoldingAmount(holding["asset-holding"].amount/bigInt(1e6).value);
  } else {
    console.log("Wallet not connected.")
  }
};

const sendPixels = async ({ cells, address, setCells, changes, holdingAmount, setHoldingAmount, setHoldingString, setContractState }) => {
  let receiver = "FGU3MBZAUFYVYO2JC33UBHGNDEOFNA5LSMXERVUO6MJEBGH6RN3OYACOTM";  // app creator address
  if (address) {
    if (changes <= holdingAmount) {
      // go through the cells with update: false, generate txns for each of those, sign and send all at once via myalgo
      // TODO: check to make sure wallet is connected
      
      // first organize changes by row, TODO: break up each row into blocks of 16
      let pos = {};
      let cols = {};
      for (let i = 0; i < cells.length; i++) {
        if (!cells[i].update) {
          let row = String(Math.floor(i/100)+1);
          if (row in pos) {
            if (pos[row][pos[row].length-1].length<16) {
              pos[row][pos[row].length-1].push(i%100);
              cols[row][cols[row].length-1].push(colors.indexOf(cells[i].color));
            } else {
              pos[row].push([i%100]);
              cols[row].push([colors.indexOf(cells[i].color)]);
            };
            
          } else {
            pos[row] = [[i%100]];
            cols[row] = [[colors.indexOf(cells[i].color)]];
          };
        };
      };

      let params = await algodClient.getTransactionParams().do()
      params.fee = 1000;
      params.flatFee = true;

      let rows = Object.keys(pos);
      let gtxns = [];
      for (let i = 0; i < rows.length; i++) {
        for (let j = 0; j < pos[rows[i]].length; j++) {
          let app1 = []
          app1 = concatUint8Array(pos[rows[i]][j], app1);
          let app2 = []
          app2 = concatUint8Array(cols[rows[i]][j], app2);
          const appArgs = [
            new Uint8Array(Buffer.from("px")),
            new Uint8Array(app1),  // either 1-100 (r1-47) or 1-61 (r48)
            new Uint8Array(app2),
            new Uint8Array(Buffer.from(rows[i]))  // double check that you don't need to run parseInt
          ];

          let attxn = {
            ...params,
            fee: 1000,
            flatFee: true,
            type: "axfer",
            from: address,
            to: receiver,
            amount: pos[rows[i]][j].length*1000000,
            assetIndex: assetID,
          };

          let nptxn = {
            ...params,
            fee: 1000,
            flatFee: true,
            type: "appl",
            from: address,
            appIndex: appID,
            appArgs: appArgs,
            appOnComplete:0,
          };
          let txns = [attxn, nptxn];
          gtxns = [...gtxns, ...txns];
        };
      };

      if (gtxns.length) {
        setContractState("Making txns (takes at most 25s)")
        try {
          // group
          for (let i = 0; i < gtxns.length; i += 2) {
            const group = gtxns.slice(i, i + 2);
            const groupId = algosdk.computeGroupID(group);
            for (const txn of group) {
              txn.group = groupId;
            }
          };

          // Sign txns.
          const blobs = await myAlgoWallet.signTransaction(gtxns).then((gtxns) =>
            gtxns.map((txn) => txn.blob)
          );

          // Create jobs.
          const blobGroups = [];
          for (let i = 0; i < blobs.length; i += 2) {
            const group = blobs.slice(i, i + 2);
            blobGroups.push(group);
          };

          const jobs = blobGroups.map(
            (group) => async () =>
              algodClient
                .sendRawTransaction(group)
                .do()
                .then(({ txId, message }) => txId || Promise.reject(message))
          );
          
          setContractState("Updating smart contract...")
          // Do jobs.
          for (const job of jobs) {
            // Try to do the job.
            try {
              await job();
            } catch (err) {
              alert(err);
              return;
            }
          };

          setHoldingString(String(holdingAmount - bigInt(changes).value));
          setHoldingAmount(holdingAmount - bigInt(changes).value);

          // if transaction goes through, wait 4s and reset cells and clear state
          await new Promise(r => setTimeout(r, 3800));
          for (let i = 0; i < cells.length; i++) {
            cells[i].update = true;
          };
          setCells(cells)

          setContractState("Picture Updated!")
          await new Promise(r => setTimeout(r, 2000));
          setContractState("")
        } catch (error) {
          console.log(error);
          setContractState("")
        };
      };
    } else {
      console.log("Not enough funds");
    }
  } else {
    console.log("Wallet not connected.")
  };
};

